<template lang="html">
  <div class="z-backtop-wrapper" @click="goTop" :class="{'show-top': isShowTop}" :style="{'right': right, 'bottom': bottom}">
    <slot>
      <i class="iconfont icon-top"></i>
    </slot>
  </div>
</template>

<script>
export default {
  name: 'z-backtop',

  data() {
    return {
      isShowTop: false
    }
  },

  props: {
    // 
    acceleration: {
      type: Number,
      default: 0.5
    },

    // 
    stime: {
      type: Number,
      default: 10
    },

    scrollTop: {
      type: Number,
      default: 0
    },

    right: {
      type: String,
      default: '5%'
    },

    bottom: {
      type: String,
      default: '5%'
    }
  },

  mounted() {
    if (this.scrollTop <= 0) {
      this.isShowTop = true
      return false
    } else {
      let self = this
      addEventListener('scroll', function() {
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop || window.scrollY
        if (scrollTop >= self.scrollTop) {
          self.isShowTop = true
        } else {
          self.isShowTop = false
        }
      })
    }
  },

  methods: {
    goTop: function() {
      let self = this
      const acceleration = self.acceleration
      const stime = self.stime
      let x1 = 0
      let y1 = 0
      let x2 = 0
      let y2 = 0
      let x3 = 0
      let y3 = 0
      if (document.documentElement) {
        x1 = document.documentElement.scrollLeft || 0
        y1 = document.documentElement.scrollTop || 0
      }
      if (document.body) {
        x2 = document.body.scrollLeft || 0
        y2 = document.body.scrollTop || 0
      }
      x3 = window.scrollX || 0
      y3 = window.scrollY || 0

      // 
      let x = Math.max(x1, Math.max(x2, x3))
      // 
      let y = Math.max(y1, Math.max(y2, y3))


      //  =  / , ,  1 , 
      let speeding = 1 + acceleration
      window.scrollTo(Math.floor(x / speeding), Math.floor(y / speeding))

      // , 
      if (x > 0 || y > 0) {
        window.setTimeout(self.goTop, stime)
      }
    }
  }
};
</script>

<style lang="less">
.z-backtop-wrapper {
  position: fixed;
  z-index: 9;
  cursor: pointer;
  transition: all .2s;
  opacity: 0;
  visibility: hidden;

  i {
    font-size: 42px;
  }
}

.show-top {
  opacity: 1;
  visibility: visible;
}
</style>



